.rightsidehome {
    display: flex;
    flex-direction: column;
    padding-top: 30px;
    width: 100%;
}

.topprofile {
    display: flex;
    justify-content: space-between;
    width: 80%;
    align-items: center;
}

.imagedivright {
    width: 45px;
    height: 45px;
    border-radius: 100%;
}

.imgright {
    width: 100%;
    border-radius: 100%;
}

.usernameblock {
    display: flex;
    flex-direction: column;
}

.usernameright {
    font-size: 14px;
    font-weight: 500;
}

.userfullname {
    color: #999999;
    font-size: 14px;
}

.switchbtn {
    color: #0095F6;
    font-weight: 500;
    font-size: 12px;
    gap: 50px;
    cursor: pointer;
}

.leftrightprofile {
    display: flex;
    gap: 20px;
}

.bottomrightside {
    display: flex;
    flex-direction: column;
    width: 80%;
    margin-top: 30px;
}

.suggestedblock {
    display: flex;
    justify-content: space-between;
    color: #999999;
    font-size: 14px;
}

.seeall {
    font-weight: 600;
    color: black;
}

.followright {
    display: flex;
    flex-direction: column;
    padding-top: 20px;
    gap: 10px;
}

.topprofilebottom {
    display: flex;
    justify-content: space-between;
    width: 100%;
    align-items: center;
}