.App {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  display: flex;
}

.leftside {
  flex: 0.155;
  padding: 25px 10px;
}

.middleside {
  flex: 0.5;
  border-left: 1px solid #d8cccc;
}

.rightside {
  flex: 0.345;
}

@media screen and (min-width:920px) and (max-width:1140px) {
  .rightside {
    display: none;
  }

  .leftside {
    flex: 0.2;
  }

  .middleside {
    flex: 0.8;
  }
}

@media screen and (min-width:320px) and (max-width:920px) {
  .rightside {
    display: none;
  }

  .leftside {
    display: none;
  }

  .middleside {
    flex: 1;
  }
}