.middlehomeside {
    width: 100%;
    padding: 0px 50px;
    box-sizing: border-box;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}

.storyblock {
    width: 700px;
    height: 120px;
    display: flex;
    gap: 17px;
    align-items: center;
    overflow-x: auto;
}

.storyblock::-webkit-scrollbar {
    display: none;
}

.storyparticular {
    width: 66px;
    height: 66px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.imagediv {
    width: 66px;
    height: 61px;
    border-radius: 100%;
    border: 3px solid purple;
    box-sizing: border-box;
}

.statusimg {
    width: 60px;
    height: 56px;
    border-radius: 100%;
}

.profilename {
    font-size: 10px;
    font-family: 'Times New Roman', Times, serif;
    font-weight: 700;
}

/*-------------------------------------*/

.postsection {
    width: 60%;
}

.post {
    width: 100%;
    border-bottom: 1px solid rgb(190, 186, 186);
    margin-bottom: 20px;
    padding-bottom: 20px;
}

.postinfo {
    width: 100%;
    display: flex;
    align-items: center;
    gap: 10px;
    padding: 5px 0;
}

.postinfoimg {
    width: 30px;
    border-radius: 100%;
    border: 3px solid purple;
}

.postinfousername {
    display: flex;
    font-size: 12px;
    font-weight: 600;
}

.timinginfo {
    color: #999999;
    font-size: 14px;
}

.postimg {
    width: 100%;
}

.iconsblock {
    width: 100%;
    margin-bottom: 20px;
    padding-bottom: 12px;
}

span {
    content: "\2661";
    font-size: 45px;
}

.comment {
    align-items: center;
    width: 30px;
    padding-left: 45px;

}

.comment2 {
    width: 20px;
    padding-left: 300px;
}

.likesection {
    display: flex;
    flex-direction: row;
    align-items: flex-start;
}

.nooflikes {
    font-size: 14px;
    font-weight: 600;
    width: 100%;
    margin-left: 20px;
    margin-top: 0px;
}

.otherstuff {
    display: flex;
    flex-direction: column;
}

.likeimg,
.likeimg2 {
    width: 20px;
    height: 20px;
    border-radius: 50%;
    margin-right: -5px;
}

.likeimg2 {
    order: -1;
}

.postaboutname {
    width: 100%;
    font-size: 14px;
    font-weight: 500;
}

.comment {
    top: 815px;
    font-weight: 400;
    font-size: 14px;
    white-space: nowrap;
    padding-left: 0;
}