.navlinkpart {
    display: flex;
    flex-direction: column;
    margin-top: 30px;

}

.navlink {
    align-items: center;
    display: flex;
    padding-left: 10px;
    border-radius: 5px;
    height: 6px;
    padding: 20px;
    cursor: pointer;
    transition: background-color 0.3s;
    margin-bottom: 10px;
    font-weight: 400;

    &:hover {
        background-color: #ededed;
    }
}

